import React, { Component } from "react";
import "./Start.css";
import io from "socket.io-client";
import CircularProgress from "@material-ui/core/CircularProgress";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Photos from "./photos.png";
const socket = io();

export default class Start extends Component {
  constructor(props) {
    super(props);

    this.state = {
      _id: JSON.parse(localStorage.getItem("user")).user_id,
      sex: JSON.parse(localStorage.getItem("user")).user_sex,
      realid: JSON.parse(localStorage.getItem("user")).user_realid,
      open: false,
      open2: false,
      room_count: JSON.parse(localStorage.getItem("user")).room_count, // 
      progress: (
        <button className="Font2_start" onClick={this.onMatching}>
          {" "}
           !{" "}
        </button>
      ),
      max_room: 8, //   8
    };
  }

  componentWillMount() {
    const user = {
      userid: this.state._id,
      sex: this.state.sex,
      realid: this.state.realid,
    };

    fetch("api/onmatching", {
      ////////////////////////////////////////////////////////
      //   
      method: "post",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(user),
    })
      .then((res) => res.json())
      .then((json) => {
        this.setState({
          room_count: json.room_count,
        });
        ////////////////// //////////////////////
        if (this.state.room_count > 0) {
          //0   
          this.props.nickname_switch_false();
        }

        if (this.state.room_count < this.state.max_room) {
          //  
        } else {
          this.setState({
            progress: (
              <button className="Font_start"> </button>
            ),
          });
        }
        ////////////////////////////////////
      });

    fetch("api/CheckStart", {
      //      
      method: "post",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(user),
    })
      .then((res) => res.json())
      .then((json) => {
        if (json) {
        } else {
          this.props.nickname_switch_false();
          this.setState({
            progress: (
              <div className="Progress_start">
                <CircularProgress
                  color="secondary"
                  style={{
                    width: "100px",
                    height: "100px",
                    zIndex: "1",
                    position: "relative",
                  }}
                />
                <button className="Font2_start" onClick={this.stopMathing}>
                  
                </button>
              </div>
            ),
          });
        }
      });

    socket.on("successmatching", (matching_info) => {
      this.props.nickname_switch_false();
      ////////////////////// ///////////////
      //    x
      let temp_cnt = this.state.room_count + 1;
      this.setState({
        room_count: temp_cnt,
      });
      ////////////////////// /////////////////
      if (this.state.room_count < this.state.max_room) {
        ///  
        this.setState({
          progress: (
            <button className="Font2_start" onClick={this.onMatching}>
              {" "}
               !{" "}
            </button>
          ),
          open: true,
        });
      } else {
        this.setState({
          progress: (
            <button className="Font_start"> </button>
          ),
          open: true,
        });
      }
      //////////////////////////
    });
    socket.emit("start join", this.state._id);
  }

  goGwating = (e) => {
    window.location.replace("/gwating");
  };

  modalopen = (e) => {
    e.preventDefault();
    this.setState({
      open: true,
    });
  };
  modalclose = (e) => {
    e.preventDefault();
    this.setState({
      open: false,
    });
  };

  modalopen2 = (e) => {
    e.preventDefault();
    this.setState({
      open2: true,
    });
  };
  modalclose2 = (e) => {
    e.preventDefault();
    this.setState({
      open2: false,
    });
  };

  stopMathing = () => {
    this.setState({
      progress: (
        <button className="Font2_start" onClick={this.onMatching}>
          {" "}
           !{" "}
        </button>
      ),
      open2: true, // 
    });
    if (this.state.room_count > 0) {
      this.props.nickname_switch_false();
    } else {
      this.props.nickname_switch_true();
    }

    // alert(" ");
    //modal 
    const post = {
      _id: this.state._id,
      sex: this.state.sex,
    };

    if (this.props.count === 1) {
      fetch("api/StopMatch", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(post),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json === false) {
          } else {
          }
        });
    } else {
      fetch("api/StopMatchSame", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(post),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json === false) {
          } else {
          }
        });
    }
  };

  onMatching = () => {
    this.setState({
      progress: (
        <div className="Progress_start">
          <CircularProgress
            color="secondary"
            style={{
              width: "100px",
              height: "100px",
              zIndex: "1",
              position: "relative",
            }}
          />
          <button className="Font2_start" onClick={this.stopMathing}>
            
          </button>
        </div>
      ),
    });

    this.props.nickname_switch_false();

    const userid = {
      userid: this.state._id,
      sex: this.state.sex,
      realid: this.state.realid,
    };

    if (this.props.count === 1) {
      //
      fetch("api/CheckMatching", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(userid),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json.touserid === undefined) {
            //
            //     
          } else {
            //
            //   
            ///////////////////////////////////////////////////
            this.setState({
              room_count: json.room_count,
            });
            json.realid = userid.realid;
            this.props.nickname_switch_false();
            socket.emit("matchingtouser", json);

            if (json.room_count < this.state.max_room) {
              this.setState({
                progress: (
                  <button className="Font2_start" onClick={this.onMatching}>
                    {" "}
                     !{" "}
                  </button>
                ),
                open: true,
              });
            } else {
              this.setState({
                progress: (
                  <button className="Font_start"> </button>
                ),
                open: true,
              });
            }
            // if (this.state.sex === "M") {
            //   // 
            //   if (json.room_count < 3) {
            //     // 3 
            //     this.setState({
            //       progress: (
            //         <button className="Font2_start" onClick={this.onMatching}>
            //           {" "}
            //            !{" "}
            //         </button>
            //       ),
            //       open: true,
            //     });
            //   } else {
            //     this.setState({
            //       progress: (
            //         <button className="Font_start">
            //            
            //         </button>
            //       ),
            //       open: true,
            //     });
            //   }
            // } else {
            //   // 
            //   if (json.room_count < 5) {
            //     // 5 
            //     this.setState({
            //       progress: (
            //         <button className="Font2_start" onClick={this.onMatching}>
            //           {" "}
            //            !{" "}
            //         </button>
            //       ),
            //       open: true,
            //     });
            //   } else {
            //     this.setState({
            //       progress: (
            //         <button className="Font_start">
            //            
            //         </button>
            //       ),
            //       open: true,
            //     });
            //   }
            // }
          }
        });
    } else if (this.props.count === 2) {
      //
      fetch("api/CheckMatchingSame", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(userid),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json.touserid === undefined) {
          } else {
            this.setState({
              room_count: json.room_count,
            });
            json.realid = userid.realid;
            this.props.nickname_switch_false();
            socket.emit("matchingtouser", json);

            if (json.room_count < this.state.max_room) {
              this.setState({
                progress: (
                  <button className="Font2_start" onClick={this.onMatching}>
                    {" "}
                     !{" "}
                  </button>
                ),
                open: true,
              });
            } else {
              this.setState({
                progress: (
                  <button className="Font_start"> </button>
                ),
                open: true,
              });
            }
          }
        });
    }
  };
  componentDidMount() {}

  render() {
    return this.props.count === 1 ? (
      <div className="matching_dialog">
        <Dialog
          open={this.state.open}
          onClose={this.modalclose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title2">
            <img id="photos" src={Photos} width="30vw" height="30vw" />
            {" !"}
            <div className="photos"></div>
          </DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
                .
              <br />    .
            </DialogContentText>
          </DialogContent>
        </Dialog>
        <Dialog
          open={this.state.open2}
          onClose={this.modalclose2}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
                .
            </DialogContentText>
          </DialogContent>
        </Dialog>

        <div className="start_progress">{this.state.progress}</div>
      </div>
    ) : this.props.count == 2 ? (
      <div className="matching_dialog">
        <Dialog
          open={this.state.open}
          onClose={this.modalclose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title2">
            <img id="photos" src={Photos} width="30vw" height="30vw" />
            {" !"}
            <div className="photos"></div>
          </DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
                .
              <br />    .
            </DialogContentText>
          </DialogContent>
        </Dialog>
        <Dialog
          open={this.state.open2}
          onClose={this.modalclose2}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
                .
            </DialogContentText>
          </DialogContent>
        </Dialog>

        <div className="start_progress">{this.state.progress}</div>
      </div>
    ) : (
      <div>
        <button className="Font2_start" onClick={this.goGwating}>
          {" "}
           !{" "}
        </button>
      </div>
    );
  }
}
